.status-box {
    padding: 5px 10px;
    border-radius: 3px;
    display: inline-block;
    margin-top: 10px;
    color: #fff;
    font-size: 12px;
}

.status-box.pending {
    background-color: #ffc107; /* Yellow */
}

.status-box.in_progress {
    background-color: #17a2b8; /* Blue */
}

.status-box.completed {
    background-color: #28a745; /* Green */
}

.typeahead .form-control {
    width: 100%;
}
